}

.page {
  color: var(--primary-text-color);
  letter-spacing: .03em;
  font-family: Roboto, sans-serif;
}

h1, h2, h3, p, ul {
  margin: 0;
  padding: 0;
}

img {
  height: auto;
  width: 100%;
