  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  text-decoration: none;
}

.menu__item:hover .menu__link, .menu__item:focus .menu__link {
  color: var(--button-green);
  text-decoration: underline;
}

.button__order {
  width: 98px;
  height: 32px;
