  border-radius: 30px 0 30px 30px;
  position: absolute;
  top: 50px;
  right: 20px;
  overflow: hidden;
  transform: rotateY(-90deg);
  box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
}

